// src/components/LoginPage.tsx
"use client";
import React, { useEffect } from "react";
import { useRouter } from "next/navigation";
import { onAuthStateChanged } from "firebase/auth";
import FirebaseUIWidget from "@/components/ui/FirebaseUiWidget";
import { auth } from "@/firebase/config";

const LoginPage = () => {
  const router = useRouter();

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (user) {
        // User is signed in, redirect to /color-process
        router.push("/color-process");
      }
    });

    // Cleanup subscription on unmount
    return () => unsubscribe();
  }, [router]);

  // FirebaseUI Configuration
  const uiConfig = {
    signInFlow: "popup",
    signInSuccessUrl: "/color-process", // Redirect to your desired page
    signInOptions: [
      "google.com", // Google Provider ID (String literal)
      "password", // Email/Password Provider ID (String literal)
    ],
    callbacks: {
      signInSuccessWithAuthResult: () => false, // Prevent redirect
    },
  };

  return (
    <div>
      <h2>Login</h2>
      <FirebaseUIWidget uiConfig={uiConfig} />
      <p>
        Don&apos;t have an account? <a href="/auth/signup">Sign up</a>
      </p>
    </div>
  );
};

export default LoginPage;
